 @import './variables.scss';
 @import './mixins.scss';

 %btn {
 	color: #FFF;
 	letter-spacing: 0.01em;
 	line-height: 20px;
 	background: #366EFF;
 	box-shadow: 0px 4px 18px rgba(54, 110, 255, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
 	border-radius: 30px;
 	height: 36px;
 	@include flex(row, center, center);
 	text-align: center;
 	padding: 0px 119px;

 	&:disabled {
 		opacity: 0.7;
 		cursor: no-drop;
 	}
 }